/************************/
/* ROW SETTINGS */
/************************/
.row {
  width: 100%;
  margin: 0 auto;
  font-size: 0; }
  .row--contained {
    max-width: 1400px; }
  .row--flex {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row; }

